
body, html {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
body {
  font-size: 16px;
  line-height: 1.42;
}
.wrapper {
  overflow: hidden;
  min-height: 100%;
  position: relative;
}
#app {
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
ul {
  list-style-type: none;
}

.wrapper.main-page {
  background: url(/static/img/water.e5d6c10.jpg) 50% 50% no-repeat;
}
.main-header {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 380px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 395px;
  margin: auto;
  background: rgba(43, 65, 44, 0.6);
  color: #fff;
  border-radius: 5px;
}
.about {
  padding: 40px;
}
.about-img {
    position: relative;
    display: block;
    width: 141px;
    height: 141px;
    margin: auto;
    border-radius: 50%;
    overflow: hidden;
    background: #ccc;
}
.about-img-src {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      margin: auto;
      display: block;
}
.about-ttl {
    font-size: 28px;
    font-weight: 500;
}
.about-subttl {
    font-size: 16px;
}
.social-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 22px;
}
.social-item {
  width: calc(100% / 3);
  text-indent: -9999px;
}
.social-link {
  width: 100%;
  height: 100%;
  display: block;
}
.main-nav {
  width: 100%;
}
.nav-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  height: 45px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-left: 0;
}
.nav-item {
  width: calc(100% / 3);
  height: 100%;
  background: #649257;
  border-right: 2px solid rgba(255, 255, 255, 0.6);
  cursor: pointer;
  -webkit-transition: background .8s;
  transition: background .8s;
}
.nav-item:first-child {
    border-bottom-left-radius: 5px;
}
.nav-item:last-child {
    border-bottom-right-radius: 5px;
    border-right: none;
}
.nav-item:hover {
    background: #487f39;
}
.nav-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 100%;
  color: #fff;
  text-decoration: none;
}



















































